@charset "utf-8";
/*////////////////////////////////////////////////////

	ページごとの個別スタイル
	※サイト規模によってはさらにファイルを分けること

////////////////////////////////////////////////////*/

/* ------------------------------------------------
	トップページここから
------------------------------------------------ */
.l-box{
  opacity:0;
  @include transition(all,.5s .0s, ease);
  @include flexbox;
  section{
    @include flexbox;
  }
}

.contents-list{
  width:auto;
  height:auto;
  @include flexbox;
  @include flex-direction($direction:column);
  flex: 1;
  img{
    width:100%;
    height:100%;
  }
  .box-contents{
    background:var(--bg-color03);
    padding:20px;
    @include flexbox;
    // @include flex-justify-content($justify:center);
    // @include flex-align($align: center);
    @include flex-direction($direction:column);
    flex: 1;
  }
  .date{
    font-size:var(--fSizeXXS);
    color:var(--font-color02);
    margin-bottom:8px;
  }
  .list-title{
    font-size:var(--fSizeS);
    margin:10px 0 15px 0;
    a{
      display:inline-block;
      color:var(--font-color01);
    }
  }
  .box-img{
    width:100%;
    background:var(--bg-color08);
  }
}


.mvtype{
  font-size:var(--MVfont);
  @include english($weight: bold);
  &:after{
    content:'|';
    position:relative;
    left:10px;
    font-weight:normal;
    @include animation(mvtype .8s ease 0s infinite normal);
    @include keyframes(mvtype){
      0% {
        opacity: 0;
      }
      50%{
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
  }
  @include media(sp){
    font-size:clamp(26px, 1vw, 30px);
  }
}

.mvimg{
  // position: relative;
  // z-index: -1;
  // top: 36px;
  @include media(sp){
    img{
      margin:0 auto;
      width:80%;
      height:100%;
    }
  }
}

.mv-box{
  font-size:var(--MVfont);
  @include english($weight: bold);
  // @include animation($property);
  display:flex;
  // @include flex-justify-content($justify:center);
  @include flex-align($align: center);
  @include flex-direction($direction:column);
  width:100%;
  position:relative;
  height:66px;
  // border:1px solid #333;
  overflow:hidden;
  p{
    position:absolute;
    opacity: 0;
    top:35px;
  }
  .mv-txt-from{
    @include animation(mvtxt01 2s cubic-bezier(0, 0.58, 0.25, 1) 0s 1 normal);
    @include keyframes(mvtxt01) {
      0% {
        opacity:0;
        top: 35px;
      }
      20% {
        opacity:1;
        top: -18px;
      }
      80% {
        top: -18px;
        opacity:1;
      }
      100% {
        top: -50px;
        opacity:0;
        display:none;
      }
    }
  }         
}

.mvanimation{
    @include animation(mvtxt01 2s cubic-bezier(0, 0.58, 0.25, 1) 0 1 normal);
    @include keyframes(mvtxt01) {
      0% {
        opacity:0;
        top: 35px;
      }
      20% {
        opacity:1;
        top: -18px;
      }
      80% {
        top: -18px;
        opacity:1;
      }
      100% {
        top: -50px;
        opacity:0;
      }
    }
}


/* ------------------------------------------------
  使い回しアイテム
------------------------------------------------ */

.category-tag{
    @include flexbox;
    @include flex-justify-content($justify:flex-start);
    @include flex-wrap($wrap:wrap);
    column-gap:15px;
    row-gap:5px;
  .detail-aside & {
    margin-top:0px;
    padding:8px 0 8px 0;
  }
  li{
    font-size:var(--fSizeSS);
    position:relative;
    @include english;
    // &:before{
    //   content:'';
    //   display:inline-block;
    //   position:absolute;
    //   background:var(--font-color05);
    //   width:10px;
    //   height:2px;
    //   top:-5px;
    //   left:0px;
    // }
    a{
      color:var(--font-color01);
    }
  }
}
.type-tag{
  margin-top: auto;
  line-height: 1;
  @include flexbox;
  column-gap:8px;
  border-top: 1px solid var(--bg-color07);
  padding-top: 20px;
  @include flex-justify-content($justify:flex-end);
  @include flex-wrap($wrap:wrap);
  row-gap:22px;
  li{
    font-size:var(--fSizeXXS);
    @include english;
    
    a{
      color:var(--font-color01);
    }    
    &:last-child{
      margin-right:0;
    }
  }
  .detail-aside & {
    border:none;
    padding:0px 0 16px 0;
    margin-top:10px;
    @include flex-justify-content($justify:flex-start);
  }
}


/* ------------------------------------------------
  一覧ページここから
------------------------------------------------ */

.entry-box{
  margin: 140px 0 180px 0;
  .l-list & {
    margin: 80px 0 80px 0;
  }
  @include media(sp){
    margin: 40px 0 40px 0;
  }
}

.inner-entry-box{
  width:70%;
  margin:0 auto;
  font-size:var(--Enfont);
  @include media(tablet){
    width:90%;
  }
  @include media(sp){
    width:100%;
  }
  .date{
    font-size:var(--fSizeXXS);
    color:var(--font-color02);
    margin-bottom:30px;
    @include media(sp){
      margin-bottom:10px;
    }
  }
}



