    @include sticky;
    top:120px;
  }
  .detail-img{
    img{
      width:100%;
      height:auto;
    }
  }
  .detail-info{
    margin-top:20px;
    margin-bottom:10px;
    border-bottom:1px solid var(--border-color01);
