      font-size:var(--fSizeS);
      @include english;
      // padding:7px 0 ;
      // display:table;
      width:100%;
      margin-bottom:10px;
      &:nth-child(odd){
        background:var(--bg-color01);
      }
    }
    .agenda{
      font-size:var(--fSizeS);
      @include english($weight: bold);
      display:block;
      // width:120px;
      position:relative;
      // display:table-cell;
